import ReactEcharts from 'echarts-for-react'
import * as echarts from 'echarts/core';

export function LightenDarkenColor(col,amt) {
    var usePound = false;
    if ( col[0] == "#" ) {
        col = col.slice(1);
        usePound = true;
    }

    var num = parseInt(col,16);

    var r = (num >> 16) + amt;

    if ( r > 255 ) r = 255;
    else if  (r < 0) r = 0;

    var b = ((num >> 8) & 0x00FF) + amt;

    if ( b > 255 ) b = 255;
    else if  (b < 0) b = 0;
    
    var g = (num & 0x0000FF) + amt;

    if ( g > 255 ) g = 255;
    else if  ( g < 0 ) g = 0;

    return (usePound?"#":"") + (g | (b << 8) | (r << 16)).toString(16);
}


export function getDefaultGradientColor(color)
{
//var c1= LightenDarkenColor(color,10);
var c2= LightenDarkenColor(color,50);

//color=c2;
//c2=color;
    return new echarts.graphic.LinearGradient(0, 0, 1, 0, [
        { offset: 0, color: c2 },
        { offset: 0.5, color: color },
     
        { offset: 1, color: c2 }
      ]);
}
export function stringToColour(str) {
    var hash = 0
    for (let i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash)
    }
    var colour = '#'
    for (let i = 0; i < 3; i++) {
      var value = (hash >> (i * 8)) & 0xff
      colour += ('00' + value.toString(16)).substr(-2)
    }

    return getDefaultGradientColor(colour)

    //return colour
  }


  export function componentToHex(c) {
    var hex = c.toString(16);
    return hex.length == 1 ? "0" + hex : hex;
  }
  
  export function rgbToHex(r, g, b) {
    return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
  }

  export function stringToSparqlColorValue(query,value)
  {
       try
       {
         var s=  query.split("#color:RGBA(");
         for (let n in s)
         {
          if (n==0)continue;
            let t=s[n].split(")@\"");
            let rgb=t[0];
            let name =t[1].split("\";")[0];
            name=name.replace("\"","");
            rgb=rgb.split(",");
 
            if (name.includes(value) )
            {
              let hex =this.rgbToHex(parseInt(rgb[0]),parseInt(rgb[1]),parseInt(rgb[2]));
             // console.log("found hex "+hex+"  "+ value)
              return hex;
            
            }

         }
       }
       catch(e)
       {console.log(e);}

       console.log("color not found reverting to string color");
       return stringToColour(value);
  }

  
