  get3ParameterDataStacked() 
  {
    var par1 = this.props.data.head.vars[0]
    var par2 = this.props.data.head.vars[1]
    var par3 = this.props.data.head.vars[2]

    var sorting = {}
    var par2Sorting = {}
    var xseries = []
    var previousX = null

    for (var n in this.props.data.results.bindings) {
      var binding = this.props.data.results.bindings[n]
      var id = binding[par1].value
      if (id !== previousX) {
        previousX = id
        xseries.push(id)
      }
      var o = sorting[id]
      if (o == null) {
        o = {}
        sorting[id] = o
      }
      o[binding[par2].value] = binding[par3].value
      par2Sorting[binding[par2].value] = ''
    }

    var series = []
    var legend = []
    for (let n in par2Sorting) {
      var serie = []
      legend.push(n)
      for (let i in xseries) {
        o = sorting[xseries[i]]
        var value = o[n]
        if (value == null) {
          value = 0
        }
        serie.push(value)
      }
      var serieO = {
        name: n,
        type: 'bar',
        stack: 'yes',
        barWidth: '75%',
        data: serie,
      }
      series.push(serieO)
    }

    var option = {
      series: series,
      xAxis: [
        {
          type: 'category',
          data: xseries,
        },
      ],
      yAxis: [
        {
          type: 'value',
        },
      ],
    }

    option.legend = {
      data: legend,
    }

    return option
  }

