  getOption() 
  {
    // console.log(this.props.charttype,this.props.data);
    try {
      if (this.props.data == null) {
        return {}
      }
      if (this.props.charttype === 'BarStacked') {
        return this.getColBarOptionStacked(true)
      }
      if (this.props.charttype === 'ColStacked') {
        return this.getColBarOptionStacked(false)
      }

      var data = this.props.data
      var gdata = null
      if (data.head.vars.length === 1) {
        gdata = this.get1ParameterData()
      }
      if (data.head.vars.length === 2) {
        gdata = this.get2ParameterData()
      }
      if (data.head.vars.length === 3) {
        gdata = this.get3ParameterData()
      }
      if (data.head.vars.length === 4) {
        gdata = this.get4ParameterData()
      }
      if (data.head.vars.length === 5) {
        gdata = this.get5ParameterData()
      }
      
      if (data.head.vars.length === 6) {
        gdata = this.get6ParameterData()
      }
      if (data.head.vars.length === 7) {
        gdata = this.get7ParameterData()
      }
      if (data.head.vars.length === 8) {
        gdata = this.get8ParameterData()
      }
      if (data.head.vars.length === 9) {
        gdata = this.get9ParameterData()
      }
      if (data.head.vars.length === 10) {
        gdata = this.get10ParameterData()
      }


      if (gdata != null) {
        if (this.props.charttype === 'Pie') {
          return this.getPieOption(gdata, false,false)
        }
        if (this.props.charttype === 'SmallPie') {
          return this.getPieOption(gdata, false,true)
        }
        if (this.props.charttype === 'Doughnut') {
          return this.getPieOption(gdata, true)
        }
        if (this.props.charttype === 'DoughnutSmall') {
          return this.getPieOption(gdata, true,true)
        }
        if (this.props.charttype === 'Bar') {
          return this.getColBarOption(gdata, true)
        }
        if (this.props.charttype === 'Col') {
           var o=this.getColBarOption(gdata, false)
          
          return o;
        }
        if (this.props.charttype === 'gauge') {
          return this.getGaugeOption(gdata, false)
        }
        console.log('geen charttype aanwezig')
      }
    } catch (error) {}

    return {}
  }
  getGaugeOption(data)
  {
   // console.log(data);
    var option={
      tooltip: {
        formatter: '{a} <br/>{b} : {c}%'
      },
      series: [
        {
        
          type: 'gauge',
          progress: {
            show: true
          },
          detail: {
            valueAnimation: true,
            formatter: '{value}'
          },
          data: data
        }
      ]
    };
    this.setOptionJSON(option);
    return option;
  }


