   parseWithUnquotedKeys(jsonString) {
    // Regular expression to match unquoted keys
    const unquotedKeysRegex = /[{,]\s*([a-zA-Z_$][0-9a-zA-Z_$]*)\s*:/g;

    // Replace unquoted keys with quoted keys
    var fixedJsonString = jsonString.replace(unquotedKeysRegex, (match, p1) => {
        return `{"${p1}":`;
    });
    fixedJsonString=fixedJsonString;
         // console.log(fixedJsonString);
    // Parse the fixed JSON string
    return JSON.parse(fixedJsonString);
}




  setOptionJSON(option) {
    var optionOrg=option;
   if (this.props.optionOptions!=null)
   {
        try
        {
          let target =JSON.parse(this.props.optionOptions);
          option=_.merge(option, target);
        
        }
        catch(e)
        { 
           try
           {
              let target =this.parseWithUnquotedKeys(this.props.optionOptions);
              option=_.merge(option, target);
           }
          catch(e)
          { 

           console.log(e,this.props.optionOptions);
          }
        }
     }  
     
     if (option==null) return optionOrg;

     
    return option
  }





