  arraytojson(array)
  {
     var json={};
    
     for (let n in array)
     {
        var nn=parseInt(n);
        var value=array[n];
        
        if (value.startsWith("#")) continue;
       
        json[value]=array[(nn+1)];
     }

     return json;
  }
getDefaultColors()
{
    var defaultColor=['#4cabce', '#006699', '#4cabce', '#e5323e','#003366','#ff00aa','#ff1111','#aabbcc','#ccbbaa','#11ff11','#1111ff'];

    if (this.props.gradientColors)
    {
      var ar=[];
      for (var n in defaultColor )
      {
           var c = getDefaultGradientColor(defaultColor[n]);
           ar.push(c);
      }
      defaultColor=ar;
    }
    return defaultColor;
   
}


  getColor(series,gradient)
  {
   
   var defaultColor=this.getDefaultColors();

   //console.log("get color2 ",series,this.props.color);

    var n=0;
    var color=[];
    try
    {
    if (series==null){console.log("series ==null. do not know what is going on "); return defaultColor;} 
    if (this.props.color!=null)
    {
     // var colorHash=this.arraytojson(this.props.color);
    
     var colorHash=this.arraytojson(this.props.color);
  //   console.log(colorHash);
       
         for (let seriesN in series)
         {
          var serie = series[seriesN];
          var tcolor=null;
          try {tcolor=colorHash[serie];}catch(e) {}
          if (tcolor==null) {      try {tcolor=colorHash[serie.name];}catch(e) {} }
          
      //    console.log(serie,tcolor);
          if (tcolor==null)
          {
            try
            {
             
                color.push(defaultColor[n]); // normal or gradient
              
              n++;
            }catch(ee){
              if (gradient)
              {
                
                color.push(getDefaultGradientColor("#ffaa11"));
              }
              else
              {
                color.push("#ffaa11")
              }
            }
          }
          else
          {
            if (gradient)
              {
                  
                  color.push(getDefaultGradientColor(tcolor))
              }
              else
              {
               
                color.push(tcolor)
              }
          }

         }
    }
  }
  catch(e){
    color=defaultColor;
  }
  

  return color;
  }

