stringToSparqlColor(query,option)
  {
  //  #color:RGBA(200,0,0,1)@"uitgesteld"
  try {
    if (this.props.charttype === 'Pie') {
      option.color = []
      let data = option.series[0].data

      for (let n in data) {
        option.color.push(stringToSparqlColorValue(query,data[n].name))
      }
     
      return;
    }
  } catch (e) {}

  try {
    if ((this.props.charttype === 'Bar') || (this.props.charttype === 'Col'))  {
     // console.log(this.props.charttype, 'process string color', option)

     

      let data = null;
      try {data=option.legend.data;} catch(e){}
      if (data==null) data=option.xAxis[0].data;
     
        for (let nn in option.series)
        {
          let ndata = []
          for (let n in data) {
            let o = { value: option.series[nn].data[n] }
            let x={ color: stringToSparqlColorValue(query,data[n]) };
            o.itemStyle =x
            ndata.push(o);

          }
          option.series[nn].data = ndata;
      }
      
     
      return;
    }
  } catch (e) {console.log(e);}

}




 processStringToColor(option) {
    //pie

    try {
      if (this.props.charttype === 'Pie') {
        option.color = []
        let data = option.series[0].data

        for (let n in data) {
          option.color.push(stringToColour(data[n].name))
        }
        return
      }
    } catch (e) {}

    try {
      if (this.props.charttype === 'Bar') {
       // console.log(this.props.charttype, 'process string color', option)

        let data = option.legend.data
        let ndata = []
        for (let n in data) {
          let o = { value: option.series[0].data[n] }
          o.itemStyle = { color: stringToColour(data[n]) }
          ndata.push(o)
        }
        option.series[0].data = ndata
        return
      }
    } catch (e) {}
  }


